@import "components/variables";
@import 'components/generales/banner-dudas.scss';

.section-team {
  .img-team {
    padding: 1rem;
  }
  .slick-next:before,
  .slick-prev:before {
    display: none;
  }
  p.title-team {
    color: $gray-siroco;
    font-weight: 400;
    font-family: Roboto;
    span {
      font-weight: 800;
      color: $blue-siroco;
    }
  }
  .inf-team {
    background-image: linear-gradient(to bottom, #ffffff, #f4f6fe, #e4effd, #d0e9fb, #b9e4f5);
    padding: 2rem;
    border: 1px solid $gray-claro;
    border-radius: 15px;
    li {
    color: $gray-siroco;
      span {
        font-weight: 800;
      }
    }
  }
  .slider-for {
    position: relative;
    &:before {
      content: "";
      display: block;
      width: 0;
      height: 0;
      border-top: 25px solid transparent;
      border-bottom: 25px solid #F6FBFF;
      border-right: 25px solid transparent;
      position: absolute;
      top: -8%;
      left: 8.5rem;
      border-left: 25px solid transparent;
      @media (max-width: 1399.98px) {
        top: -8%;
        left: 7.5rem;
      }
      @media (max-width: 1199.98px) {
        top: -8%;
        left: 5.5rem;
      }
      @media (max-width: 991.98px) {
        top: -8%;
        left: 4rem;
      }
      @media (max-width: 767.98px) {
        top: -6%;
        left: 90px;
      }
      @media (max-width: 479.98px) {
        top: -6%;
        left: 12rem;
      }
      @media (max-width: 424.98px) {
        top: -4%;
        left: 8rem;
      }
    }
  }
  ul {
    padding-left: 0;
    li {
      color: $gray-siroco;
      list-style: none;
      span {
        font-weight: 800;
      }
      a {
        color: $gray-siroco;
        text-decoration: none;
        word-break: break-all;
        font-weight: 800;
        transition: all 0.3s;
        &:hover {
          color: $blue-siroco;
        }
      }
    }
  }
}
